import React, { useEffect, useState } from 'react';
import axios from 'axios';
import { useAuth } from '../context/AuthContext';
import WalletOverview from '../components/WalletOverview';
import TransactionList from '../components/TransactionList';
import PortfolioChart from '../components/PortfolioChart';

const DashboardPage = () => {
    const { userId } = useAuth();
    const [wallet, setWallet] = useState(null);
    const [transactions, setTransactions] = useState([]);
    const [allCryptos, setAllCryptos] = useState([]); // Za cene i simbole
    const [loading, setLoading] = useState(true);
    const [sendAmount, setSendAmount] = useState('');
    const [sendCurrency, setSendCurrency] = useState('BTC');
    const [receiverUsername, setReceiverUsername] = useState('');
    const [sendError, setSendError] = useState('');
    const [sendSuccess, setSendSuccess] = useState('');

    useEffect(() => {
        const fetchData = async () => {
            if (!userId) {
                setLoading(false); // Prestani sa uitavanjem ako nema korisnika
                return;
            }

            setLoading(true);
            try {
                // Uitaj novanik korisnika
                const walletRes = await axios.get(`http://localhost:5000/wallets?userId=${userId}`);
                if (walletRes.data.length > 0) {
                    setWallet(walletRes.data[0]);
                } else {
                    // Ovo bi trebalo da se desi pri registraciji, ali kao fallback
                    const newWallet = { userId, balances: {} };
                    const createdWallet = await axios.post('http://localhost:5000/wallets', newWallet);
                    setWallet(createdWallet.data);
                }

                // Uitaj transakcije za korisnika (kao poiljaoca ili primaoca)
                const txRes = await axios.get(`http://localhost:5000/transactions?senderId=${userId}`);
                const receivedTxRes = await axios.get(`http://localhost:5000/transactions?receiverId=${userId}`);

                // Sortiraj sve transakcije po datumu (najnovije prvo)
                const allUserTxs = [...txRes.data, ...receivedTxRes.data].sort((a, b) => new Date(b.date) - new Date(a.date));
                setTransactions(allUserTxs);

                // Uitaj sve podrane kriptovalute
                const cryptoRes = await axios.get('http://localhost:5000/cryptos');
                setAllCryptos(cryptoRes.data);

                // Postavi default valutu za slanje ako korisnik ima neke balanse
                if (walletRes.data.length > 0 && Object.keys(walletRes.data[0].balances).length > 0) {
                    setSendCurrency(Object.keys(walletRes.data[0].balances)[0]);
                }

            } catch (error) {
                console.error("Greka pri uitavanju podataka:", error);
            } finally {
                setLoading(false);
            }
        };

        fetchData();
    }, [userId]);

    const handleSendCrypto = async (e) => {
        e.preventDefault();
        setSendError('');
        setSendSuccess('');

        const amount = parseFloat(sendAmount);
        if (isNaN(amount) || amount <= 0) {
            setSendError('Unesite validan iznos.');
            return;
        }

        if (!wallet || !wallet.balances[sendCurrency] || wallet.balances[sendCurrency] < amount) {
            setSendError(`Nedovoljno sredstava za ${sendCurrency}. Trenutno imate: ${wallet?.balances[sendCurrency] || 0} ${sendCurrency}`);
            return;
        }

        // Zabraniti slanje samom sebi
        const senderUsername = (await axios.get(`http://localhost:5000/users/${userId}`)).data.username;
        if (receiverUsername === senderUsername) {
            setSendError('Ne moete slati kriptovalute samom sebi.');
            return;
        }


        try {
            // Pronai primaoca po username-u
            const receiverRes = await axios.get(`http://localhost:5000/users?username=${receiverUsername}`);
            if (receiverRes.data.length === 0) {
                setSendError('Primalac sa unetim korisnikim imenom ne postoji.');
                return;
            }
            const receiverUser = receiverRes.data[0];
            const receiverUserId = receiverUser.id;

            // Pronai novanik primaoca
            let receiverWalletRes = await axios.get(`http://localhost:5000/wallets?userId=${receiverUserId}`);
            let receiverWallet = receiverWalletRes.data[0];

            if (!receiverWallet) {
                // Kreiraj novanik za primaoca ako ne postoji
                receiverWallet = { userId: receiverUserId, balances: {} };
                receiverWallet = (await axios.post('http://localhost:5000/wallets', receiverWallet)).data;
            }

            // Auriraj stanje poiljaoca
            const updatedSenderBalances = { ...wallet.balances };
            updatedSenderBalances[sendCurrency] -= amount;

            // Auriraj stanje primaoca
            const updatedReceiverBalances = { ...receiverWallet.balances };
            updatedReceiverBalances[sendCurrency] = (updatedReceiverBalances[sendCurrency] || 0) + amount;

            // Transakcija poiljaoca (PATCH jer menjamo postojei novanik)
            await axios.patch(`http://localhost:5000/wallets/${wallet.id}`, { balances: updatedSenderBalances });

            // Transakcija primaoca (PATCH jer menjamo postojei novanik)
            await axios.patch(`http://localhost:5000/wallets/${receiverWallet.id}`, { balances: updatedReceiverBalances });

            // Kreiraj transakciju
            const newTransaction = {
                id: `tx${Date.now()}`, // Jedinstveni ID za transakciju
                senderId: userId,
                receiverId: receiverUserId,
                amount: amount,
                currency: sendCurrency,
                date: new Date().toISOString(),
                type: 'send'
            };
            await axios.post('http://localhost:5000/transactions', newTransaction);

            setSendSuccess('Kriptovaluta uspeno poslata!');
            setSendAmount('');
            setReceiverUsername('');

            // Auriraj lokalno stanje novanika
            setWallet(prevWallet => ({ ...prevWallet, balances: updatedSenderBalances }));

            // Ponovo dohvati transakcije za sve prikaz
            const txRes = await axios.get(`http://localhost:5000/transactions?senderId=${userId}`);
            const receivedTxRes = await axios.get(`http://localhost:5000/transactions?receiverId=${userId}`);
            const allUserTxs = [...txRes.data, ...receivedTxRes.data].sort((a, b) => new Date(b.date) - new Date(a.date));
            setTransactions(allUserTxs);

        } catch (error) {
            console.error("Greka pri slanju kriptovalute:", error);
            setSendError('Dolo je do greke prilikom slanja. Proverite da li primalac postoji i imate li dovoljno sredstava.');
        }
    };

    if (loading) {
        return <div className="text-center text-lg mt-8">Uitavanje...</div>;
    }

    // Proveri da li je wallet null ili prazan nakon uitavanja
    if (!wallet || Object.keys(wallet.balances).length === 0) {
        return (
            <div className="py-8 text-center">
                <h1 className="text-4xl font-bold text-gray-900 mb-8">Moj Dashboard</h1>
                <p className="text-xl text-gray-700">Va novanik je trenutno prazan. Ne moete slati kriptovalute dok ne dobijete neku.</p>
                {/* Uklonjen je ceo problematini div blok za simulaciju primanja sredstava */}
            </div>
        );
    }


    return (
        <div className="py-8">
            <h1 className="text-4xl font-bold text-gray-900 mb-8 text-center">Moj Dashboard</h1>

            <div className="grid grid-cols-1 md:grid-cols-2 gap-8 mb-8">
                {wallet && <WalletOverview wallet={wallet} allCryptos={allCryptos} />}
                {wallet && <PortfolioChart wallet={wallet} allCryptos={allCryptos} />}
            </div>

            <div className="grid grid-cols-1 md:grid-cols-2 gap-8">
                {/* Forma za slanje kriptovaluta */}
                <div className="bg-white p-6 rounded-lg shadow-xl">
                    <h3 className="text-2xl font-semibold mb-4">Poalji Kriptovalutu</h3>
                    <form onSubmit={handleSendCrypto}>
                        <div className="mb-4">
                            <label className="block text-gray-700 text-sm font-bold mb-2" htmlFor="receiverUsername">
                                Korisniko ime primaoca:
                            </label>
                            <input
                                type="text"
                                id="receiverUsername"
                                className="shadow appearance-none border rounded w-full py-2 px-3 text-gray-700 leading-tight focus:outline-none focus:shadow-outline"
                                value={receiverUsername}
                                onChange={(e) => setReceiverUsername(e.target.value)}
                                required
                            />
                        </div>
                        <div className="mb-4">
                            <label className="block text-gray-700 text-sm font-bold mb-2" htmlFor="sendAmount">
                                Iznos:
                            </label>
                            <input
                                type="number"
                                id="sendAmount"
                                className="shadow appearance-none border rounded w-full py-2 px-3 text-gray-700 leading-tight focus:outline-none focus:shadow-outline"
                                value={sendAmount}
                                onChange={(e) => setSendAmount(e.target.value)}
                                step="any"
                                required
                            />
                        </div>
                        <div className="mb-6">
                            <label className="block text-gray-700 text-sm font-bold mb-2" htmlFor="sendCurrency">
                                Kriptovaluta:
                            </label>
                            <select
                                id="sendCurrency"
                                className="shadow border rounded w-full py-2 px-3 text-gray-700 leading-tight focus:outline-none focus:shadow-outline"
                                value={sendCurrency}
                                onChange={(e) => setSendCurrency(e.target.value)}
                            >
                                {/* Prikaz opcija samo za valute koje korisnik ima u novaniku */}
                                {Object.keys(wallet?.balances || {})
                                    .filter(symbol => wallet.balances[symbol] > 0) // Samo valute sa balansom > 0
                                    .map(symbol => (
                                        <option key={symbol} value={symbol}>{symbol}</option>
                                    ))}
                            </select>
                        </div>
                        {sendError && <p className="text-red-500 text-xs italic mb-4">{sendError}</p>}
                        {sendSuccess && <p className="text-green-500 text-xs italic mb-4">{sendSuccess}</p>}
                        <button
                            type="submit"
                            className="bg-indigo-600 hover:bg-indigo-700 text-white font-bold py-2 px-4 rounded focus:outline-none focus:shadow-outline transition duration-300"
                        >
                            Poalji
                        </button>
                    </form>
                </div>

                <TransactionList transactions={transactions} userId={userId} />
            </div>
        </div>
    );
};

export default DashboardPage;