import React, { createContext, useContext, useState, useEffect } from 'react';
import axios from 'axios';

const AuthContext = createContext(null);

export const AuthProvider = ({ children }) => {
    const [isAuthenticated, setIsAuthenticated] = useState(false);
    const [user, setUser] = useState(null); // Sadri userId, username, role
    const [loading, setLoading] = useState(true); // Za proveru autentifikacije pri startu

    useEffect(() => {
        // Pokuaj da se uloguje iz localStorage-a pri uitavanju aplikacije
        const storedUser = localStorage.getItem('user');
        if (storedUser) {
            try {
                const parsedUser = JSON.parse(storedUser);
                setUser(parsedUser);
                setIsAuthenticated(true);
            } catch (e) {
                console.error("Failed to parse user from localStorage", e);
                localStorage.removeItem('user');
            }
        }
        setLoading(false);
    }, []);

    const login = async (username, password) => {
        try {
            // U pravoj aplikaciji lozinke bi bile heirane na backendu
            const response = await axios.get(`http://localhost:5000/users?username=${username}&password=${password}`);
            if (response.data.length > 0) {
                const fetchedUser = response.data[0];
                const userPayload = {
                    id: fetchedUser.id,
                    username: fetchedUser.username,
                    role: fetchedUser.role
                };
                setUser(userPayload);
                setIsAuthenticated(true);
                localStorage.setItem('user', JSON.stringify(userPayload));
                return true;
            } else {
                return false; // Pogreni kredencijali
            }
        } catch (error) {
            console.error("Login error:", error);
            return false;
        }
    };

    const register = async (username, password) => {
        try {
            // Proveri da li korisnik ve postoji
            const checkUser = await axios.get(`http://localhost:5000/users?username=${username}`);
            if (checkUser.data.length > 0) {
                return false; // Korisnik ve postoji
            }

            // Dodaj novog korisnika
            const newUser = {
                id: `user${Date.now()}`, // Jednostavan ID, za pravu app koristio bi UUID
                username,
                password, // U pravoj aplikaciji bi se heovalo!
                role: 'user'
            };
            const userResponse = await axios.post('http://localhost:5000/users', newUser);
            
            // Kreiraj prazan novanik za novog korisnika
            await axios.post('http://localhost:5000/wallets', { userId: newUser.id, balances: {} });

            return true;
        } catch (error) {
            console.error("Registration error:", error);
            return false;
        }
    };

    const logout = () => {
        setIsAuthenticated(false);
        setUser(null);
        localStorage.removeItem('user');
    };

    const userId = user ? user.id : null;
    const username = user ? user.username : null;
    const userRole = user ? user.role : null;

    return (
        <AuthContext.Provider value={{ isAuthenticated, user, userId, username, userRole, login, register, logout, loading }}>
            {!loading && children}
        </AuthContext.Provider>
    );
};

export const useAuth = () => useContext(AuthContext);