import React from 'react';
import { Link } from 'react-router-dom';

const AdminDashboardPage = () => {
  return (
    <div className="min-h-[calc(100vh-80px)] flex flex-col items-center justify-center bg-gray-50 text-gray-800">
      <h1 className="text-4xl font-bold mb-8 text-indigo-700">Admin Panel</h1>
      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
        <Link
          to="/admin/users"
          className="bg-white p-6 rounded-lg shadow-md hover:shadow-lg transition duration-300 flex flex-col items-center justify-center text-center"
        >
          <span className="text-5xl mb-3"></span>
          <h2 className="text-xl font-semibold mb-2">Upravljanje Korisnicima</h2>
          <p className="text-gray-600">Kreiranje, auriranje i brisanje korisnikih naloga.</p>
        </Link>
        <Link
          to="/admin/cryptos"
          className="bg-white p-6 rounded-lg shadow-md hover:shadow-lg transition duration-300 flex flex-col items-center justify-center text-center"
        >
          <span className="text-5xl mb-3"></span>
          <h2 className="text-xl font-semibold mb-2">Upravljanje Kriptovalutama</h2>
          <p className="text-gray-600">Dodavanje, ureivanje i brisanje podranih kriptovaluta.</p>
        </Link>
        <Link
          to="/admin/transactions"
          className="bg-white p-6 rounded-lg shadow-md hover:shadow-lg transition duration-300 flex flex-col items-center justify-center text-center"
        >
          <span className="text-5xl mb-3"></span>
          <h2 className="text-xl font-semibold mb-2">Pregled Transakcija</h2>
          <p className="text-gray-600">Pregled svih transakcija u sistemu.</p>
        </Link>
      </div>
    </div>
  );
};

export default AdminDashboardPage;