import React, { useEffect, useState } from 'react';
import axios from 'axios';

const AllTransactionsPage = () => {
  const [transactions, setTransactions] = useState([]);
  const [users, setUsers] = useState([]); // Za mapiranje ID-eva na usernames
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState('');

  useEffect(() => {
    const fetchData = async () => {
      setLoading(true);
      try {
        const txResponse = await axios.get('http://localhost:5000/transactions?_sort=date&_order=desc');
        setTransactions(txResponse.data);

        const usersResponse = await axios.get('http://localhost:5000/users');
        setUsers(usersResponse.data);
      } catch (err) {
        setError('Greka pri uitavanju transakcija ili korisnika.');
        console.error(err);
      } finally {
        setLoading(false);
      }
    };
    fetchData();
  }, []);

  const getUserNameById = (userId) => {
    const user = users.find(u => u.id === userId);
    return user ? user.username : 'Nepoznat';
  };

  const handleDeleteTransaction = async (id) => {
    if (window.confirm('Da li ste sigurni da elite da obriete ovu transakciju?')) {
      try {
        await axios.delete(`http://localhost:5000/transactions/${id}`);
        setTransactions(transactions.filter(tx => tx.id !== id));
      } catch (err) {
        setError('Greka pri brisanju transakcije.');
        console.error(err);
      }
    }
  };

  if (loading) {
    return <div className="text-center text-lg mt-8">Uitavanje svih transakcija...</div>;
  }

  return (
    <div className="py-8">
      <h1 className="text-4xl font-bold text-gray-900 mb-8 text-center">Sve Transakcije</h1>
      <div className="bg-white p-6 rounded-lg shadow-xl">
        {error && <p className="text-red-500 text-xs italic mb-4">{error}</p>}
        {transactions.length === 0 ? (
          <p className="text-gray-600 text-center">Nema transakcija u sistemu.</p>
        ) : (
          <table className="min-w-full bg-white">
            <thead>
              <tr>
                <th className="py-2 px-4 border-b text-left">ID</th>
                <th className="py-2 px-4 border-b text-left">Poiljalac</th>
                <th className="py-2 px-4 border-b text-left">Primalac</th>
                <th className="py-2 px-4 border-b text-left">Iznos</th>
                <th className="py-2 px-4 border-b text-left">Valuta</th>
                <th className="py-2 px-4 border-b text-left">Datum</th>
                <th className="py-2 px-4 border-b text-left">Akcije</th>
              </tr>
            </thead>
            <tbody>
              {transactions.map(tx => (
                <tr key={tx.id} className="hover:bg-gray-50">
                  <td className="py-2 px-4 border-b text-sm">{tx.id}</td>
                  <td className="py-2 px-4 border-b">{getUserNameById(tx.senderId)}</td>
                  <td className="py-2 px-4 border-b">{getUserNameById(tx.receiverId)}</td>
                  <td className="py-2 px-4 border-b">{tx.amount}</td>
                  <td className="py-2 px-4 border-b">{tx.currency}</td>
                  <td className="py-2 px-4 border-b text-sm">{new Date(tx.date).toLocaleString()}</td>
                  <td className="py-2 px-4 border-b">
                    <button
                      onClick={() => handleDeleteTransaction(tx.id)}
                      className="bg-red-500 hover:bg-red-700 text-white text-sm py-1 px-2 rounded"
                    >
                      Obrii
                    </button>
                  </td>
                </tr>
              ))}
            </tbody>
          </table>
        )}
      </div>
    </div>
  );
};

export default AllTransactionsPage;